import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastController, ModalController, LoadingController } from '@ionic/angular';
import { debounceTime, distinctUntilChanged, filter } from 'rxjs/operators';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { DenominationSlideComponent } from 'src/app/components/denomination-slide/denomination-slide.component';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ToastService } from 'src/app/services/toast.service';
import { TokenV2Page } from '../../token-v2/token-v2.page';

@Component({
  selector: 'app-cash-deposit-v2',
  templateUrl: './cash-deposit-v2.page.html',
  styleUrls: ['./cash-deposit-v2.page.scss'],
})
export class CashDepositV2Page implements OnInit {
  @ViewChild("denom", { read: ElementRef }) denomButtonRef: ElementRef;
  title: any = 'Cash Deposit';
  productCode = "CHD";
  tokenOrigin = "Mobile";
  public form: FormGroup;
  depositType: any;
  homeIconToggle: boolean = false;
  selectedCountryCode: string;
  isShow: boolean = true;
  currencies: any = [];
  users: any[] = [];
  phoneNumber: string;
  customerId: string;
  routerData: any;

  isFrmClosed: boolean = false;
  animationState = 'out';
  frmToggle: boolean = false;
  @ViewChild('dynamic', { read: ViewContainerRef }) private cashViewRef: ViewContainerRef;

  repFG: FormGroup = this.formBuilder.group({
    personName: ['', Validators.required],
    phoneNo: ['', [Validators.required, Validators.minLength(10),
    Validators.maxLength(10)]],
    repdocumentId: [''],
    repdocumentNumber: [''],
  });
  customerDetails: any;
  editMode: boolean = false;
  selectedCurrency: any;
  accountInfo: any;
  chargeAmount: any = 5;
  selectedAccount: any;
  currentUser: any;
  filteredAccount: any = [];
  isShowDenomError: boolean = false;
  transAmount: any;
  isShowRepError: boolean = false;
  cashDepositResp: any;
  transactionId: any;

  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private api: ApiService,
    public datepipe: DatePipe,
    public toastCtrl: ToastController,
    public toastService: ToastService,
    private modalController: ModalController,
    public loading: LoadingService,
    public shareDataService: DataService,
    private loadingController: LoadingController,
    private cdr: ChangeDetectorRef) { }

  ngOnInit() {
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));
    this.formInit();

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      console.log("Values?", this.routerData);
      this.editMode = this.routerData?.data ? true : false;
    }

    if (this.editMode) {
      const data = this.routerData?.data;

      const countryCode = this.currencies.filter((i: any) => i.currencyCode === data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();

      let deposit_type = data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form.get('representativeInfo').get('personName').patchValue(data?.representativeInfo?.[0]?.personName);
        this.form.get('representativeInfo').get('phoneNo').patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form.get('representativeInfo').get('repdocumentId').patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form.get('representativeInfo').get('repdocumentNumber').patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }
    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.loadData();

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
    })

    this.form.get('transactionAmount').valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('transactionAmount').valueChanges.pipe(debounceTime(1500), filter(res => res > 0), distinctUntilChanged())
      .subscribe(res => {
        this.updateTransAmount();
        this.denomValidationListener();
      });


    let today = new Date().toISOString();
    this.form.get('transactionDate').patchValue(today);

  }

  formInit() {
    this.form = this.formBuilder.group({
      transactionId: [''],
      customerId: [''],
      productCode: ['CHD', [Validators.required]],
      tokenOrigin: ['Mobile', [Validators.required]],
      accountNumber: ['', [Validators.required]],
      transactionCurrency: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required]],
      transactionDate: ['', [Validators.required]],
      transactionBranch: ['', [Validators.required]],
      isMobileTrans: [true, [Validators.required]],
      transactionTime: ['', [Validators.required]],
      totalChargeAmount: [this.chargeAmount, [Validators.required]],
      totalTransactionAmount: ['', [Validators.required]],
      branchFlag: [true],
      accountBalance: [''],
      accountCurrency: [''],
      accountBranch: [''],
      depositType: [''],
      exchangeRate: [''],
      accountAmount: [''],
      narrative: [''],
      denomination: [null],
      totalAmount: [''],
      createdBy: [''],
      createdTime: [''],
      modifiedBy: [''],
      modifiedTime: [''],
      recordStatus: [''],
      authStatus: [''],
      version: [''],
      remarks: ['']
    })
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    console.log(this.routerData?.data);
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const data = this.routerData?.data;

      const countryCode = this.currencies.filter((i: any) => i.currencyCode === data?.transactionCurrency);
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();

      let deposit_type = data?.representativeInfo.length != 0 ? 'representative' : 'self';
      this.depositType = deposit_type;
      this.form.get('depositType')?.patchValue(deposit_type);

      if (deposit_type == 'representative') {
        this.form.addControl('representativeInfo', this.repFG);
        this.form.get('representativeInfo').get('personName').patchValue(data?.representativeInfo?.[0]?.personName);
        this.form.get('representativeInfo').get('phoneNo').patchValue(data?.representativeInfo?.[0]?.phoneNo);
        this.form.get('representativeInfo').get('repdocumentId').patchValue(data?.representativeInfo?.[0]?.documentId);
        this.form.get('representativeInfo').get('repdocumentNumber').patchValue(data?.representativeInfo?.[0]?.documentNumber);
      } else {
        this.form.removeControl('representativeInfo');
      }
    } else {
      const countryCode = this.currencies.filter((i: any) => i.currencyCode === "INR");
      this.selectedCurrency = countryCode[0];
      this.depositType = 'self';
      this.form.get('depositType')?.patchValue('self');
    }

    this.formInit()
    this.loadData();

    this.shareDataService.getAccountInfo.subscribe(data => {
      this.accountInfo = data;
    });

    this.form.get('transactionAmount').valueChanges.subscribe(val => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    })

    this.form.get('transactionAmount').valueChanges.pipe(debounceTime(1500), filter(res => res > 0), distinctUntilChanged())
      .subscribe(res => {
        this.updateTransAmount();
        this.denomValidationListener();
      });

  }

  loadData() {
    this.loading.present();
    this.api.custpomerDetails(this.phoneNumber).subscribe((resp) => {
      this.loading.dismiss();
      resp.custAccount = resp.custAccount.filter(card => card.status === "APPROVED");
      this.customerDetails = resp;
      this.upsertAccount(resp);
    }, (err: any) => {
      this.loading.dismiss();
    })
  }

  getHomeBranch() {
    this.homeIconToggle = !this.homeIconToggle;
    if (this.homeIconToggle) {
      this.form.get('transactionBranch')?.patchValue(this.customerDetails?.custAccount[0]?.accountBranch);
    } else {
      this.form.get('transactionBranch')?.patchValue("");
    }
  }

  upsertAccount(filteredResponseSavingAccount) {

    this.users = filteredResponseSavingAccount.custAccount;
    this.customerId = filteredResponseSavingAccount.customerId;

    if (this.editMode) {
      const data = this.routerData?.data;
      this.form.get('accountNumber')?.patchValue(data?.accountNumber);
      this.form.get('accountCurrency')?.patchValue(data?.accountCurrency);
      this.form.get('transactionCurrency')?.patchValue(data?.transactionCurrency);
      this.form.get('exchangeRate')?.patchValue(data?.exchangeRate);
      this.form.get('transactionAmount')?.patchValue(data?.transactionAmount);
      let formatedAmount: any = this.shareDataService.formatCurrency(data?.totalTransactionAmount, data?.transactionCurrency);
      this.form.get('totalTransactionAmount').patchValue(formatedAmount);
      this.form.get('transactionBranch')?.patchValue(data?.transactionBranch);
      this.form.get('transactionDate')?.patchValue(data?.transactionDate);
      this.form.get('transactionTime')?.patchValue(data?.transactionTime);
      this.form.get('remarks')?.patchValue(data?.remarks);
    } else {
      if (this.accountInfo.accountId != null) {
        this.form.get('accountNumber').patchValue(this.accountInfo.accountId);
      } else {
        this.form.get('accountNumber').patchValue(this.users[0].accountId);
      }
      this.selectedCountryCode = this.selectedCurrency.countryCode.toLowerCase();
      this.form.get('transactionCurrency')?.patchValue(this.selectedCurrency.currencyCode);
      this.form.controls.accountBranch.patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
      this.form.get('transactionBranch').patchValue(filteredResponseSavingAccount.custAccount[0].accountBranch);
    }
    this.cdr.markForCheck();
  }

  keyPressMob(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onAccountSelect(e: any) {
    console.log("selected", this.selectedAccount);
    let filteredAccount: any = this.users.filter(account => account.accountId === this.selectedAccount);
    this.filteredAccount = filteredAccount[0];
    const countryCode = this.currencies.filter((i: any) => i.currencyCode === this.filteredAccount?.accountCurrency);
    console.log("countryCode", countryCode);
    this.selectedCurrency = countryCode[0];
    this.selectedCountryCode = this.selectedCurrency?.countryCode.toLowerCase();
    this.form.get('transactionCurrency')?.patchValue(this.filteredAccount?.accountCurrency);
  }


  getChargeDetails() {
    this.api.getChargeDetails(this.accountInfo.accountId).subscribe(resp => {
      this.chargeAmount = resp?.[0]?.amount;
      this.form.get('totalChargeAmount').patchValue(resp?.[0]?.amount);
    }, err => console.log('Error: ', err))
  }


  goBack() {
    this.router.navigate(['dashboard']);
  }

  enableDisableAction(type: any) {
    switch (type) {
      case 'TXAM':
        this.toggleFrm();
        break;
      default:
        console.log("No such type exists!");
        break;
    }
  }

  toggleFrm() {
    this.frmToggle = !this.frmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponent();
  }

  createComponent() {
    this.cashViewRef.clear();
    let childComponentRef = this.cashViewRef.createComponent(DenominationSlideComponent);
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = "cashViewRef";
    childComponent.cashDepositRef = this;
  }

  removeDynamicComponent(): void {
    this.cashViewRef.clear();
  }

  remove(view: any) {
    console.log("view disiss?", view);
    let data = JSON.parse(localStorage.getItem(view)) || null;
    console.log("Data??", data);
    if (view == 'cashViewRef') {
      this.cashViewRef.clear();
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        console.log(this.form.value);
      }
    }
  }

  denomAmountValidation(amount: any) {
    if (Number(this.form.get('transactionAmount')?.value) != Number(amount)) {
      this.isShowDenomError = true;
      this.toastService.showToast("Denomination total should match with the transaction amount");
    } else {
      this.isShowDenomError = false;
    }
    this.cdr.markForCheck();
  }

  denomValidationListener() {
    let data = JSON.parse(localStorage.getItem('cashViewRef')) || null;
    console.log("Data??", data);
    if (data?.title == 'cashViewRef') {
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        console.log(this.form.value);
      }
    }
  }

  segmentChanged(e: any) {
    if (e == 'representative') {
      this.form.addControl('representativeInfo', this.repFG);
    } else {
      this.form.removeControl('representativeInfo');
    }
    console.log(this.form.value);
  }


  openPopup() {
    this.modalController.create({
      component: TimeSlotsComponent,
      componentProps: {
        date: this.form.get('transactionDate').value,
      }
    }).then(modalResp => {
      modalResp.present()
      modalResp.onDidDismiss().then(res => {
        if (res.data != null) {
          console.log(res);
          this.form.get('transactionTime').patchValue(res.data);
        }
      })
    })
  }

  // Only Integer Numbers
  keyPressNumbers(event) {
    var charCode = (event.which) ? event.which : event.keyCode;
    // Only Numbers 0-9
    if ((charCode < 48 || charCode > 57)) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  selectCurrencyCode(e: any) {
    console.log(e);
    if (typeof e !== "undefined") {
      const filteredCurrency = this.currencies?.filter((i: any) => i.currencyCode === e);
      this.selectedCountryCode = filteredCurrency[0].countryCode.toLowerCase();
      this.getExChangeRate(filteredCurrency[0].currencyCode);
      this.cdr.markForCheck();
    } else {
      return;
    }
  }

  getExChangeRate(currency) {
    this.api.getExchangeRate(this.accountInfo.accountCurrency).subscribe((res: any) => {
      const code = this.currencies.filter((x: any) => x.currencyCode == currency);
      let filteredCurrency = code[0].currencyCode;
      const rate = res?.rates?.[filteredCurrency];
      this.form.get('exchangeRate').patchValue(rate);
      if (!isNaN(Number(this.transAmount))) {
        let updatedAmount: number = (Number(this.transAmount) * Number(this.form.get('exchangeRate')?.value)) + this.chargeAmount;
        if (!isNaN(Number(updatedAmount))) {
          console.log(Number(updatedAmount));
          let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.form.get('transactionCurrency')?.value);
          this.form.get('totalTransactionAmount').patchValue(formatedAmount);
          this.cdr.markForCheck();
        }
      }
    });
    this.updateTransAmount();
  }

  updateTransAmount() {
    if (!isNaN(Number(this.transAmount))) {
      let updatedAmount: number = (Number(this.transAmount) * Number(this.form.get('exchangeRate')?.value)) + this.chargeAmount;
      if (!isNaN(Number(updatedAmount))) {
        console.log(Number(updatedAmount));
        let formatedAmount: any = this.shareDataService.formatCurrency(updatedAmount, this.form.get('transactionCurrency')?.value);
        this.form.get('totalTransactionAmount').patchValue(formatedAmount);
        this.cdr.markForCheck();
      }
    }
  }


  submit(form: any) {

    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.customerId = this.customerId;
    form.productCode = this.productCode;
    form.accountType = this.filteredAccount?.accountType;
    form.tokenOrigin = this.tokenOrigin;
    form.accountCurrency = this.filteredAccount?.accountCurrency || "";

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    if (this.depositType == 'representative') {
      form.representativeInfo = [{
        phoneNo: this.form.get('representativeInfo').get('phoneNo')?.value,
        personName: this.form.get('representativeInfo').get('personName')?.value,
        documentId: this.form.get('representativeInfo').get('repdocumentId')?.value,
        documentNumber: this.form.get('representativeInfo').get('repdocumentNumber')?.value
      }]
    } else {
      form.representativeInfo = []
    }

    form.isMobileTrans = true;

    // Total transaction convertion format
    let ttam = this.form.value['totalTransactionAmount'].substring(1);
    form.totalTransactionAmount = ttam.replace(/,/g, '');

    if (this.editMode) {
      this.api.appointmentBooking(form).subscribe((resp) => {
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.cashDepositResp = resp;
        this.transactionId = this.cashDepositResp.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        console.log('transactionId::', this.transactionId);
        if (this.cashDepositResp === 200 || this.cashDepositResp !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.form.reset();
          this.onClick(this.cashDepositResp);
        }
      });
    } else {
      form.transactionAmount = form.transactionAmount.toString().replace(/,/g, '');
      this.api.cashDepositSave(form).subscribe((resp) => {
        localStorage.setItem("TransactionTime", resp.transactionTime);
        this.cashDepositResp = resp;
        this.transactionId = this.cashDepositResp.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        console.log('transactionId::', this.transactionId);
        if (this.cashDepositResp === 200 || this.cashDepositResp !== null) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.form.reset();
          this.onClick(this.cashDepositResp);
        }
      });
    }
  }

  goToHomepage() {
    this.router.navigate(['/dashboard']);
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: TokenV2Page,
      componentProps: {
        value: event,
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log("Getting Back Data", this.routerData);
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: "branchModal",
      componentProps: {
      }
    });

    modal.onDidDismiss().then((modelData) => {
      if (modelData !== null) {
        this.form.get('transactionBranch').patchValue(modelData.data['data'].branchName);
      }
    });

    return await modal.present();
  }

  ngOnDestroy(): void {
    localStorage.removeItem("cashViewRef");
  }

}
